import galerryTemplate from '../template/templates.hbs';
import api from './api.js';

const form = document.querySelector('.search-form');
const button = document.querySelector('.more');
const input = document.querySelector('input')


form.addEventListener('submit', formSubmit);
button.addEventListener('click', onButtonclick);


function formSubmit(e) {
    e.preventDefault();
    const searchQuery = e.currentTarget.elements.query.value;
    clearGallery()
    if (searchQuery !== '') {
        api.fetchCard(searchQuery, page).then(galerryMarkuplist)
    }
    
}
function galerryMarkup(data) {
    const galleryList = document.querySelector('.gallery').insertAdjacentHTML('beforeend',galerryTemplate(data)) 
}
function clearGallery() {
    const galleryList = document.querySelector('.gallery').innerHTML = "";
}
let page = 1;
function onButtonclick(searchQuery) {
    searchQuery = inputValue()
    page++
    console.log(page);
    api.fetchCard(searchQuery, page).then(galerryMarkup);
    setTimeout(scrollPage,500)
}
console.log(page);
function inputValue() {
    return input.value
}
function galerryMarkuplist(data) {
    const morelist = document.querySelector('.gallery').insertAdjacentHTML('beforeend', galerryMarkup(data))
}
function scrollPage() {
    let position = 0;
    window.scrollTo({
        top: document.querySelector('body').scrollHeight,
        left: 0,
        behavior:'smooth',
    })
}

